import { createLogger, format, transports } from "winston";
import chalk from "chalk";
const customSimpleFormat = (colorizeText) => format.printf(({ level, label, timestamp, message }) => {
    const formattedLevel = formatLevel(level);
    return `${colorizeText(`[${label}]`, chalk.green)} ${timestamp} ${colorizeText(formattedLevel, getLevelColor(level))} ${message}`;
});
// Helper function to format log levels
const formatLevel = (level) => {
    switch (level) {
        case "info":
            return "INF";
        case "warn":
            return "WRN";
        case "error":
            return "ERR";
        case "debug":
            return "DEBUG";
        default:
            return level.toUpperCase();
    }
};
// Helper function to get color based on log level
const getLevelColor = (level) => {
    switch (level) {
        case "info":
            return chalk.green;
        case "warn":
            return chalk.yellow;
        case "error":
            return chalk.red;
        case "debug":
            return chalk.blue;
        default:
            return chalk.white;
    }
};
const LOG_LEVEL = process.env.LOG_LEVEL || "info";
// Initialize logger function
export const initLogger = () => {
    const colorizeText = (text, color) => color(text); // Default color for level
    return createLogger({
        format: format.combine(
        // format.errors({ stack: true }),
        format.timestamp({ format: "YYYY-MM-DD HH:mm:ss" }), format.label({ label: "ts-grpc-hmac" }), customSimpleFormat(colorizeText)),
        transports: [
            new transports.Console({
                level: LOG_LEVEL
            })
        ]
    });
};
